*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 14px;
}

.cold{
  background: url('./assets/winter.jpg');
  background-size: cover;
  background-position: bottom;
}

.warm{
  background: url('./assets/summer.jpg');
  background-size: cover;
  background-position: bottom;

}
 .app{  
  background:url('./assets/middle.jpg');
  background-size: cover;
  background-position: bottom;
}

main{
  background: linear-gradient(rgba(0, 0, 0, .2),rgba(0, 0, 0, .75));
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.search{
  margin: 0 0 75px;
  display: grid;
  grid-template-columns:minmax(200px,12fr)  minmax(70px,1fr);
}

.search__bar{
  display: block;
  padding: 15px;
  font-size: 1.5rem;;
  border:none;
  outline:none;
  background: rgba(255, 255, 255, .5);
  border-radius: 0 0 0 16px;
  box-shadow: 0 5px rgba(0, 0, 0, .5);
  transition: ease 0.1s;
}

.search__bar:focus{
  background: rgba(255, 255, 255, .75);
}

.button{
border-radius: 0 0 16px 0;
box-shadow: 0 5px rgba(0, 0, 0, .5);
display: inline-block;
color:rgb(219, 210, 210);
background-color: rgb(243, 112, 97);
text-decoration: none;
display: flex;
justify-content: center;
align-items: center;
font-size: 1.2rem;
font-family: 'Varela Round', sans-serif;
transition: ease 0.2s;
cursor: pointer;
}
.button:active{
  background-color: rgb(248, 75, 55);
  color:white
}
.location{
  color:white;
  text-align: center;
  font-family: 'Architects Daughter', cursive;
}

.location__place{
  font-weight: 700;
  font-size: 3rem;
  text-shadow: 3px 3px rgba(50, 70, 50, .5);
}

.location__date{
  font-weight: 500;
  font-size: 1.5rem;
  text-shadow: 2px 2px rgba(50, 50, 70, .5);
}

.weather__temperature{
  position: relative;
  display: inline-block;
  margin-top: 5%;
  padding: 15px 25px;
  background-color: rgba(255, 255, 255, .2);
  text-shadow: 5px 5px rgba(50, 70, 50, .5);
  box-shadow: 3px 6px rgba(0, 0, 0, .2);
  font-family: 'Varela Round', sans-serif;
  font-size: 7rem;
  font-weight: 900;
  border-radius: 16px;
}

.weather__type{
  font-size: 4rem;
  text-shadow: 3px 5px rgba(50, 50, 70, .5);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2%;
}

.image{
  width: 125px;
  height: 125px;
  margin-top: -20px;
}



 /* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .search{
    padding: 0 60px;
  }
  .location__place{
    font-size: 2rem;
  }
  .weather__temperature{
    font-size: 6rem;
  }
  .weather__type{
    font-size: 3rem;
  }
}

/* Landscape phones and smaller */
@media (max-width: 480px) {
  .search{
    padding: 0;
  }
  .location__place{
    font-size: 1.5rem;
  }
  .location__date{
    font-size: 1rem;
  }
  .weather__temperature{
    font-size: 4rem;
  }
  .weather__type{
    font-size: 2rem;
  }
}